
.questions {
    background: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    /* margin: 26px auto 0; */
    max-width: 550px;
    height: 650px;
    padding: 24px;
    /* position: relative; */
    width: 80%;
    font-family: 'Rajdhani', sans-serif;
}

.questions-text {
  margin: 20px;
}

.questions:before,
.questions:after {
    content: "";
    height: 98%;
    position: absolute;
    width: 100%;
    z-index: -1;
}

.questions:before {
    background: #fafafa;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
    left: -5px;
    top: 4px;
    transform: rotate(-2.5deg);
}

.questions:after {
    background: #f6f6f6;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    right: -3px;
    top: 1px;
    transform: rotate(1.4deg);
}